import gql from "graphql-tag";

export const updatedIssue = gql`
    mutation updatedIssue($id: ID! $status: String, $user_id: ID!){
        updatedIssue(
            input:{
                id: $id
                status: $status
                user_id: $user_id
            }
        ) {
            id
            status

        }
    }
`

export  class updatedIssuePayload {
    public id: string;
    public status: string;

    constructor(id: string, status: string, public user_id: any) {
        this.id = id;
        this.status = status;
    }

    response(){
        return {...this}
    }

}


// chat

export const createdMessage = gql`
    mutation createdMessage($type: MessageEnum! $text: String $room_id: ID!
        $room_user_id: ID! $is_response: Boolean! $ref_message: Int $quick_replies: Json $file: Upload
        $file_ext: String
    ){
        createdMessage(
            input:{
                type:$type
                text:$text
                room_id: $room_id
                room_user_id: $room_user_id
                is_response:$is_response
                ref_message: $ref_message
                quick_replies:$quick_replies
                file: $file
                file_ext: $file_ext
            }
        ){
            id
            type
            text
            file
            file_ext
            status
            ref_message
            audio
            size
            duration
            file_name
            init_day
            reference_response{
                id
                type
                text
                file
                file_ext
                status
            }

        }
    }
`



