import 'vue-advanced-chat/dist/vue-advanced-chat.css'

import {Component, Vue} from "vue-property-decorator";
import {RoomInterface, User} from "@/models/room";
import {createdMessage} from "@/graphql/mutations";
import {createdRoomMessage} from "@/graphql/subscription";

@Component({
  components:{
    ChatWindow
  }
})
export default class  IssueRoom extends Vue{

  prueba(e){
    console.log(e.target.files[0])
  }
  public rooms : Room[]= []
  public messages= []
  public currentUserId= ""
  public messageSelectionActions= [{ name: 'deleteMessages', title: 'Delete' }]
  public styles = { container: { borderRadius: '4px' } }
  public templatesText= [
    {
      tag: 'help',
      text: 'This is the help'
    },
    {
      tag: 'action',
      text: 'This is the action'
    },
    {
      tag: 'action 2',
      text: 'This is the second action'
    }
  ]

  public messagesLoaded = false;

  public textMessages={
    ROOMS_EMPTY: 'Sin conversacin',
    ROOM_EMPTY: 'Ninguna conversacin seleccionada',
    NEW_MESSAGES: 'Nuevas publicaciones',
    MESSAGE_DELETED: 'Este mensaje ha sido eliminado\n',
    MESSAGES_EMPTY: 'Sin mensajes',
    CONVERSATION_STARTED: 'La conversacin comenz el:',
    TYPE_MESSAGE: 'Escribe tu mensaje',
    SEARCH: 'Buscar',
    IS_ONLINE: 'En linea',
    LAST_SEEN: 'ltima conexin ',
    IS_TYPING: 'esta escribiendo...',
    CANCEL_SELECT_MESSAGE: 'Cancelar seleccin'
  }

  fetchMessages({ room, options = {} }) {
    console.log(room)
    console.log(options)
    this.messagesLoaded = true;

  }


  getRoomCurrentUser(room: RoomInterface): User {
    const roomData = room.users.find(value => value.role == "admin")!
    console.log(roomData)
    this.currentUserId = roomData.id
    return roomData;
  }

  getRoomMessages(room: RoomInterface): Message[]{
    return room.messages.data.map(value => {
      var men:Message = {
        _id: value.id,
        content: value.text,
        date: value.created_at,
        avatar: value.user.owner.firstname,
        seen: true,
        username: value.user.owner.firstname,
        timestamp: value.created_at,
        senderId: value.user.id,
        system: false,
        files: []
      }

      if(value.file){
        let menFile: MessageFile = {
          size: value.size,
          duration: value.duration,
          audio: value.audio,
          url: value.file!,
          name: value.file_name!,
          type: "audio/mp3",
          preview: value.file!
        }
        men.files?.push(menFile)
      }

      return men;
    });
  }

  getRoomParse(room: RoomInterface) : Room[]{

    return [{
      roomId: room.id,
      roomName: room.title,
      avatar: "",
      users: room.users.map((value) => {
        let user: RoomUser = {
          _id: value.id,
          username: value.owner.firstname,
          avatar: "prueba",
          status: {
            state: value.role == "admin" ? "online": "offline",
            lastChanged: ""
          }
        }
        return user
      }),
    }]
  }

  onMessageAdded (previousResult, { subscriptionData }) {
    console.log(previousResult)
    console.log(subscriptionData)

    let newResult = {
      ...previousResult
    }

    newResult.roomsOne.messages.data.push(subscriptionData.data.createdRoomMessage)
    console.log("newResult")
    console.log(newResult)

    return newResult

  }

  created(){
    // const observer = this.$apollo.subscribe({
    //   query: createdRoomMessage,
    // })
    // observer.subscribe({
    //   next (data) {
    //     console.log(data)
    //   },
    //   error (error) {
    //     console.log(error)
    //   },
    // })
  }




  async sendMessage({ content, roomId, files, replyMessage }) {
    let type = "TEXT";
    let size = 0;
    let duration = 0;
    let name: any = null;
    // const message = {
    //   sender_id: this.currentUserId,
    //   content,
    //   timestamp: new Date()
    // }

    let formData = new FormData();

     if(files.length > 0){
       type = "FILE"
       size = files[0].size;
       duration = files[0].duration;
       name = files[0].name;

       formData.append("file", files[0].blob, name);
       console.log(formData.get("file"))

     }
    // if (replyMessage) {
    //   message.replyMessage = {
    //     _id: replyMessage._id,
    //     content: replyMessage.content,
    //     sender_id: replyMessage.senderId
    //   }
    //   if (replyMessage.files) {
    //     message.replyMessage.files = replyMessage.files
    //   }
    // }
    // const { id } = await firestoreService.addMessage(roomId, message)
    // if (files) {
    //   for (let index = 0; index < files.length; index++) {
    //     await this.uploadFile({ file: files[index], messageId: id, roomId })
    //   }
    // }
    // firestoreService.updateRoom(roomId, { lastUpdated: new Date() })






    console.log(files[0].blob)

    await this.$apollo.mutate({
      mutation: createdMessage,
      variables: {
        type, text: content, room_id: roomId,
        room_user_id: this.currentUserId, is_response: false, ref_message: null, quick_replies: null,
        file: type == "FILE"? formData.get("file") : null,
        file_ext: null,
        audio: type == "FILE" , size, duration
      }
    })

  }


  formattedFiles(files: any[]) {
    const formattedFiles: any[] = []
    files.forEach(file => {
      const messageFile = {
        name: file.name,
        size: file.size,
        type: file.type,
        extension: file.extension || file.type,
        url: file.url || file.localUrl,
        audio: false,
        duration: 0,
      }
      if (file.audio) {
        messageFile.audio = true
        messageFile.duration = file.duration
      }
      formattedFiles.push(messageFile)
    })
    return formattedFiles
  }


}
